
* {
    margin: 0;
    background-color: black;
}

.title {
    color: aqua;
    padding: 10px 15px;
}

.form {
    padding: 10px 15px
}

.input {
    width: 270px;
    height: 22px;
    border: 3px solid aqua;
    border-radius: 3px;
    background-color: black;
    color: white;
    font-size: 16px;
}

.button-search, .button-reset {
    color: darkgray;
    font-weight: bold;
    font-size: 16px;
    background-color: aqua;
    opacity: 0.8;
    border-radius: 5px;
    height: 29px;
    width: 65px;
    margin-left: 10px;
    padding-top: 0;
    border: none;
}

.list-anime {
    margin: 10px 15px;
    list-style: none;
    border: 4px solid pink;
    border-radius: 6px;
    border-top-width: 40px;
    padding: 15px 2px;
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}

.title-anime {
    color: white
}

.list-anime-favorite {
    margin: 10px 15px;
    width: 300px;
    list-style: none;
    border: 4px solid white;
    border-radius: 6px;
    border-top-width: 40px;
    padding-top: 15px; 
}

.title-favorite {
    color: white;
    text-align: center;
    font-size: 24px;
    transform: translate(100px, 10px);
}

.title-results {
    color: pink;
    width: 500px;
    text-align: center;
    font-size: 24px;
}

.wrapper-favorite {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.wrapper-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.favorite {
    border: 5px solid lightseagreen;
    font-family: Arial, Helvetica, sans-serif;
}

.list-favorite {
    color: pink;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.js-single-element {
    position: relative;
    width: 250px;
}

.js-remove-item-button {
    background-color: black;
    color: red;
    font-weight: bold;
    font-size: 24px;
    border: none;
    cursor: pointer;
}

